/*resets*/
*{
    margin: 0;
    padding: 0;
    font-size: 100%;
    border: none;
    box-sizing: border-box;
    font-family: 'Source Sans Pro', sans-serif;
}

body{
    /* font: Arial, Helvetica, sans-serif;
    background: #333; */
    background: rgb(75, 75, 75);
    color: #fff;
}

img{
    max-width: 100%;
}

/*=-=-=*/
.navbar{position:relative;display:flex;flex-wrap:wrap;align-items:center;justify-content:space-between;padding-top:.5rem;padding-bottom:.5rem}
.navbar-expand{flex-wrap:nowrap;justify-content:flex-start}
.navbar-dark a:hover{color:#fff}
.bg-primary{background-color:#0d6efd!important}
.container{width:100%;padding-right:.75rem;padding-right:var(--bs-gutter-x,.75rem);padding-left:.75rem;padding-left:var(--bs-gutter-x,.75rem);margin-right:auto;margin-left:auto}
.me-auto{margin-right:auto!important}
.navbar-nav{display:flex;flex-direction:row;padding-left:0;margin-bottom:0;list-style:none}
.nav-link{padding:.5rem 1rem;color:rgb(138, 190, 254);text-decoration:none;transition:color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out}
/*-=-=*/

.main-content
{
    background-color: rgb(90, 90, 90);
    padding: 1% 1% 1% 1%;
    margin: 1% 1% 15% 1%;
    height: 90%;
    position: absolute;
    width: -moz-available;
}

.page-title
{
    text-align: left;
    font-size: 3rem;
    margin: 0% 0% 1% 0%;
}

